.homepage {
    font-size: 1.2rem;
    width: 100%;
    min-height: 100vh;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    background-size: cover;
    background-position: center;
    z-index: -1;

    &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.55);
     }

    .production-list{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 2;

        .production-card {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 30rem;
            height: 25rem;
            margin: 2rem;
            position: relative;

            &:active {
                .production-card__description {
                    display: block;
                }
            }

            &__image {
                position: absolute;
                z-index: -5;
                width: 100%;
                height: 100%;
                background-position: center;
                background-size: cover;
            }

            &__title {
                text-shadow: 0px 0px 5px darken($color-primary-black, 10),
                             0px 0px 10px darken($color-primary-black, 10),
                             0px 0px 15px darken($color-primary-black, 10);
            }

            &__description {
                display: none;
                text-align: center;
                padding: .9rem;
                margin: 0.7rem;
                border-radius: 2.5rem;
                background-color: rgba(darken($color-primary-black, 5), 0.5);
            }
        }
    }
}
